.app__social {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  padding: 1rem;
  margin-top: 1rem;
  // width: 20%;

  div {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: var(--white-color);
    margin: 0.25rem 0.25rem;
    // border: 1px solid var(--gray-color);

    display: flex;
    justify-content: center;
    align-items: center;

    transition: all 0.3s ease-in-out;

    svg {
      width: 15px;
      height: 15px;
      color: var(--purple-color);
    }

    &:hover {
      background-color: var(--sea-green-dark);
      border-color: var(--purple-color);

      svg {
        color: var(--white-color);
      }
    }

    @media screen and (min-width: 2000px) {
      width: 70px;
      height: 70px;

      margin: 0.5rem 0;

      svg {
        width: 30px;
        height: 30px;
      }
    }
    @media screen and (max-width: 450px) {
      width: 25px;
      height: 25px;

      // margin: 0.5rem 0;

      svg {
        width: 12px;
        height: 12px;
      }
    }
  }
}

.app__navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  padding: 1rem;

  .app__navigation-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #cbcbcb;
    margin: 0.5rem;

    transition: background-color 0.2s ease-in-out;

    &:hover {
      background-color: var(--sea-green-dark);
    }

    @media screen and (min-width: 2000px) {
      width: 20px;
      height: 20px;
    }
  }
}

